import * as React from "react";
import { Link, useHistory } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "../../reduxsss/hook";
import UserMgtViewModel from "../../_app/shared/ViewModels/UserMgtViewModel";
import { useLoginMutation } from "../redux/authApiSlice";
import { setCredentials } from "../redux/authSlice";
import { logins } from "../redux/_redux/authCrud";
import { connect } from "react-redux";
import * as auth from "../redux/_redux/authRedux";
import { actions } from "../redux/_redux/authRedux";

interface SignInViewModel {
  email: string;
  password: string;
}

function SignInContent() {
  const [viewModel, setViewModel] = React.useState<SignInViewModel>({
    email: "",
    password: "",
  });

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setViewModel({
      ...viewModel,
      [event.target.name]: event.target.value,
    });
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    // const { email, password } = viewModel;
    const { email, password } = state;

    logins({ email: email, password: password })
      .then((response) => {
        console.log(response.data);
        var data = response.data;

        if (data.status === "Success") {
          // dispatch(actions.login(data.access_token));
          history.push("/dashboard");
        }
        // console.log(response.data);
      })
      .catch((error) => {
        console.log(error);
      });

    // const data = { email, password };
    // dispatch(setCredentials(state));

    // const clientsViewModelsInstance = new UserMgtViewModel("signin");
    // const signIn = clientsViewModelsInstance.signIn(data);
    // dispatch(setCredentials({ ...signIn, email }));
    // signIn.then((response: any) => {
    //   if (response.status == 200) {
    //     location.href = "/";
    //   } else {
    //     location.href = ".";
    //   }
    // });
  };

  const userRef = React.useRef<HTMLInputElement>();
  const errRef = React.useRef<HTMLInputElement>();
  const [state, setState] = React.useState<SignInViewModel>({
    email: "",
    password: "",
  });
  const [errMsg, setErrMsg] = React.useState("");
  const [login, { isLoading }] = useLoginMutation();
  const dispatch = useAppDispatch();
  const history = useHistory();

  // const { user } = useAppSelector((state) => ({
  //   user: state.auth.user,
  // }));
  // console.log({ auth: user });

  React.useEffect(() => {
    userRef.current.focus();
  }, []);

  React.useEffect(() => {
    setErrMsg("");
  }, [state]);

  const handleSubmits = async (e: any) => {
    e.preventDefault();

    const { email, password } = state;

    console.log({ state: state });

    try {
      const userData = await login({ email, password }).unwrap();
      console.log("srtart ln 68");
      dispatch(setCredentials({ ...userData, email }));
      setState({ email: "", password: "" });
      console.log("navigate ln 68");
    } catch (err) {
      if (!err?.originalStatus) {
        setErrMsg("No server Response");
      } else if (err.originalStatus?.status === 400) {
        setErrMsg("Missing Email or Password");
      } else if (err.originalStatus?.status === 401) {
        setErrMsg("Unauthorized");
      } else {
        setErrMsg("Login Failed");
      }
      errRef.current.focus();
    }
    console.log("end ln 83");
  };
  const handleEmail = (e) =>
    setState({ email: e.target.value, password: state.password });
  const handleWord = (e) =>
    setState({ email: state.email, password: e.target.value });

  const content = isLoading ? (
    <h1>loading...</h1>
  ) : (
    <div className='mt-8 sm:mx-auto sm:w-full sm:max-w-md'>
      <div className='bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10'>
        <p ref={errRef} className={errMsg ? "" : ""}></p>
        <form
          className='space-y-6'
          action='#'
          method='POST'
          onSubmit={handleSubmit}
        >
          <div>
            <label
              htmlFor='email'
              className='block text-sm font-medium text-gray-700'
            >
              Email address
            </label>
            <div className='mt-1'>
              <input
                id='email'
                // onChange={handleChange}
                onChange={handleEmail}
                value={state.email}
                ref={userRef}
                // name='email'
                type='email'
                autoComplete='email'
                required
                className='block w-full appearance-none rounded-md border border-gray-300 px-3 py-2 placeholder-gray-400 shadow-sm focus:border-indigo-500 focus:outline-none focus:ring-indigo-500 sm:text-sm'
              />
            </div>
          </div>

          <div>
            <label
              htmlFor='password'
              className='block text-sm font-medium text-gray-700'
            >
              Password
            </label>
            <div className='mt-1'>
              <input
                id='password'
                // onChange={handleChange}
                onChange={handleWord}
                value={state.password}
                // name='password'
                type='password'
                autoComplete='current-password'
                required
                className='block w-full appearance-none rounded-md border border-gray-300 px-3 py-2 placeholder-gray-400 shadow-sm focus:border-indigo-500 focus:outline-none focus:ring-indigo-500 sm:text-sm'
              />
            </div>
          </div>

          <div className='flex items-center justify-between'>
            <div className='flex items-center'>
              <input
                id='remember-me'
                name='remember-me'
                type='checkbox'
                className='h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-500'
              />
              <label
                htmlFor='remember-me'
                className='ml-2 block text-sm text-gray-900'
              >
                Remember me
              </label>
            </div>

            <div className='text-sm'>
              <Link
                to='/forgetpassword'
                className='font-medium text-indigo-600 hover:text-indigo-500'
              >
                Forgot your password?
              </Link>
            </div>
          </div>

          <div>
            <button
              type='submit'
              className='flex w-full justify-center rounded-md border border-transparent bg-indigo-600 py-2 px-4 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2'
            >
              Sign in
            </button>
          </div>
        </form>

        <div className='mt-6 flex items-center justify-between'>
          <div className='text-sm'>
            <Link
              to='/sign-up'
              className='font-medium text-indigo-600 hover:text-indigo-500'
            >
              Not yet registered? Enroll
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
  return content;
}

export default connect(null, auth.actions)(SignInContent);
