import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";
import { logOut, setCredentials } from "../../member/redux/authSlice";

const baseQuery = fetchBaseQuery({
  baseUrl: "https://dev-api.sety.io/api/v1/",
  credentials: "include",
  prepareHeaders: (headers, { getState }: any) => {
    const token = getState().auth.token;
    if (token) {
      headers.set("authorization", `Bearer ${token}`);
      // headers.set("authorization", `Bearer ${token}`,);
    }
    return headers;
  },
});

const baseQueryWithReauth = async (arg, api, options) => {
  let result: any = await baseQuery(arg, api, options);

  if (result?.error?.originalStatus === 403) {
    console.log("sending refresh token");

    const refreshResult: any = await baseQuery("/refresh", api, options);
    console.log(refreshResult);

    if (refreshResult?.data) {
      const user = api.getState().auth.user;
      api.dispatch(setCredentials({ ...refreshResult.data, user }));

      result = await baseQuery(arg, api, options);
    } else {
      api.dispatch(logOut("/"));
    }
  }
  return result;
};

export const apiSlice = createApi({
  baseQuery: baseQueryWithReauth,
  endpoints: (builder) => ({}),
});
